/* * {
  outline: 1px solid red;
} */

:root {
  --shape-side: "";
  --polygon-color: #235880;
}

body {
  min-height: 100vh;
  user-select: none;
}

#root {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #69dd29;
}

.container {
  position: relative;
  width: 80%;
  height: 90%;
  background-color: #fff;
  border-radius: 0 20px 20px 20px;
  display: flex;
  position: relative;
  flex-direction: column;
}

.container .title {
  position: absolute;
  background-color: #fff;
  transform: translateY(-50%);
  color: #3d3d3d;
  border-radius: 90px;
  width: 300px;
  height: 80px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  padding: 1rem 2.5rem;
}

.container .title p {
  font-size: 1.5rem;
  text-align: end;
}

.container .title span {
  text-align: right;
  font-size: 0.8rem;
}

#canvas {
  width: 100%;
  height: 89.3%;
  /* background-color: rgba(255, 0, 0, 0.397); */
  position: absolute;
}

.grid {
  flex: 8.5;
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  padding: 3rem 0 1rem;
  /* background-color: #fff; */
}

/* 
.q-text {
  margin-bottom: auto;
} */

.table-wrapper {
  margin-top: 1rem;
}

.q-text,
.table-wrapper,
.aplabel,
.labelip,
.user-ip {
  position: relative;
  z-index: 100;
  background-color: #fff;
}
/* SHOW LABELS */

.grid .label-sec {
  position: relative;
  background-color: #fff;
}

.labelip {
  position: absolute;
  bottom: 0;
  left: 105%;
  width: fit-content;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.labelip > div {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.labelip input {
  width: 25px;
  height: 25px;
  accent-color: white;
  outline: 0.1vw solid black !important;
}

.labelip label {
  font-size: 1.1rem;
  font-weight: 600;
}

/* AREA-PERIMETER IP */

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 0;
}

input:focus {
  outline: none;
}
.aplabel {
  display: flex;
  width: 450px;
  margin-top: 1rem;
  justify-content: space-between;
  gap: 2rem;
}

.aplabel .apip {
  flex: 1;
  display: flex;
  align-items: center;
  font-weight: 600;
}

.aplabel input[type="checkbox"] {
  width: 25px;
  height: 25px;
  margin-right: 0.5rem;
  accent-color: white;
  outline: 0.1vw solid black !important;
}
.aplabel input[type="text"] {
  width: 50%;
  border: none;
  border-bottom: 2px solid black;
  text-align: center;
  font-size: 1.5rem;
  font-weight: 600;
}

table.table {
  width: 450px;
  aspect-ratio: 1/1;
  border-collapse: collapse;
  border-width: 2px;
  border-color: #808080;
  border-style: solid;
}

table.table td {
  width: calc(450px / 12);
  height: calc(450px / 12);
  border-width: 2px;
  border-color: #808080;
  border-style: solid;
  text-align: center;
  cursor: default;
  user-select: none;
  font-size: 0.8rem;
}

.hideGrid {
  border-width: 0 !important;
}

td.filled {
  /* background-color: #122f3f !important; */
  color: #fff !important;
  border-color: #fff !important;
  border-style: dotted !important;
  border-width: 1px !important;
}

.user-ip {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2rem;
  width: 450px;
  gap: 1rem;
  position: relative;
}

input {
  border: none;
}
input:focus {
  outline: none;
}

.submit-response input {
  width: 100%;
  font-size: 2rem;
  font-weight: 600;
  padding: 1rem;
  border-radius: 10px;
  text-align: center;
  /* background: rgba(0, 128, 0, 0.727); */
}

input:disabled {
  cursor: not-allowed;
}

.submit-response {
  position: relative;
  width: 95%;
  box-shadow: 0 3px 10px #0000006c;
  overflow: hidden;
  border-radius: 10px;
}

.submit-response .confirm {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  font-size: 2rem;
  display: none;
  background-color: #fff;
}

.confirm .icon {
  font-size: 4rem;
}
.green {
  color: #00ab00;
}
.red {
  color: rgb(197, 66, 66);
}
.user-ip button {
  padding: 0.6rem 2rem;
  background-color: #133754;
  color: #fff;
  font-size: 1.4rem;
  font-weight: 600;
  border: none;
  box-shadow: 0 3px 10px #0000006c;
  border-radius: 8px;
  cursor: pointer;
}

button:active {
  transform: scale(0.95);
}

/* CONTROLS  */
.controls {
  position: relative;
  flex: 1.1;
  background-color: #235880;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  border-radius: 0 0 20px 20px;
  z-index: 1;
  padding: 1rem 0;
}

.btn-wrapper {
  position: relative;
}

.color-picker {
  position: absolute;
  width: 305px !important;
  bottom: calc(100% + 2rem);
  padding: 0.5rem 0 0 0.5rem;
  background-color: rgb(230, 230, 230);
  border-radius: 5px;
  display: none !important;
  z-index: 0;
}

.show {
  display: flex !important;
  animation: colorPickerIn 0.3s;
}

@keyframes colorPickerIn {
  from {
    bottom: 100%;
    opacity: 0;
  }
  to {
    bottom: calc(100% + 2rem);
    opacity: 1;
  }
}

.controls .btn {
  display: flex;
  align-items: center;
  padding: 1.2rem 0.9rem;
  min-height: 60%;
  height: fit-content;
  font-size: 3rem;
  border-radius: 5px;

  background-color: #22577e;
  color: #fff;
  cursor: pointer;

  border: none;
  box-shadow: 5px 5px 10px #0000006b, -5px -5px 10px #ffffff2d;
}

.btn-div {
  font-size: 1.7rem !important;
  gap: 1rem;
  position: relative;
}

.btn-div h6 {
  border-right: 2px solid #fff;
  padding-right: 1rem;
  pointer-events: none;
  user-select: none;
}

.btn-invert {
  background-color: #fff !important;
  color: #22577e !important;
}

.dpd {
  position: absolute;
  z-index: 999 !important;
  left: -10%;
  bottom: calc(100% + 1.6rem);
  background-color: #fefefe;
  border: 1px solid lightgrey;
  box-shadow: 0 0 5px #00000034;
  color: gray;
  width: 120%;
  border-radius: 5px;

  flex-direction: column;
  padding: 0.4rem 0.4rem 0.8rem;
  cursor: default;

  display: none;
}

.polygon-dpd {
  flex-direction: row;
  flex-wrap: wrap;
  gap: 1px;
}

.dpd.show-dpd {
  display: flex;
}

.dpd::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);

  width: 35px;
  height: 35px;
  background-color: #fefefe;
  border-radius: 5px;
  clip-path: polygon(100% 0%, 100% 100%, 0% 100%);
  box-shadow: 0 0 5px #000000;
}

.dpd span#b {
  font-size: 1.2rem;
  border-bottom: 1px solid lightgray;
  padding: 0.4rem;
  padding-bottom: 0.4rem;

  user-select: none;
}

.polygon-dpd span {
  font-size: 0.8rem;
  flex-basis: calc(50% - 0.5px);
  padding: 1rem;
  display: flex;
  margin-bottom: 1rem;
  justify-content: center;
  align-items: center;
}

.polygon-dpd span:nth-child(3) {
  border: 1px solid lightgrey;
}

.dpd span:hover {
  cursor: pointer;
  font-weight: 700;
}

/* 
 labels 
*/

td {
  position: relative;
}

td * {
  position: absolute;
  color: black;

  font-size: 0.9rem;
  pointer-events: none;
}

.td-top {
  left: 50%;
  top: -50%;
  transform: translateX(-50%);
}
.td-right {
  right: -55%;
  top: 50%;
  transform: translateY(-50%);
}
.td-bottom {
  left: 50%;
  bottom: -50%;
  transform: translateX(-50%);
}
.td-left {
  position: absolute;
  left: -55%;
  top: 50%;
  transform: translateY(-50%);
}

/*

REGULAR PLOYGONS

*/

.polygons-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  display: flex;
  justify-content: center;
  align-items: center;
}

.polygons-container > span {
  width: calc(450px / 6);
  height: calc(450px / 6);

  /* outline: 1px solid red; */
  position: relative;
}

.polygons-container > span::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--polygon-color);
  opacity: 0.8;
}

.polygon .polygon-resizer {
  position: absolute;
  width: 10px;
  height: 10px;
  top: 100%;
  left: 100%;
  transform: translate(-50%, -50%);
  background-color: blue;
}

.polygon.rectangle .polygon-resizer {
  top: 75%;
}

.polygon.pentagon .polygon-resizer {
  left: 81.286893%;
}

.polygon.hexagon .polygon-resizer {
  left: 75%;
}

.polygon.rhombus .polygon-resizer {
  left: 50%;
}

.polygons-container > span::after {
  content: var(--shape-side);
  font-size: 0.9rem;
  font-weight: 600;
  background-color: white;

  position: absolute;
  top: calc(100% + 0.5rem);
  left: 50%;
  transform: translateX(-50%);
}

.polygons-container span.rectangle::before {
  clip-path: polygon(0% 25%, 100% 25%, 100% 75%, 0% 75%);
}
.polygons-container span.triangle::before {
  clip-path: polygon(50% calc(100% - (0.866025405 * 100%)), 100% 100%, 0% 100%);
}
.polygons-container span.pentagon::before {
  clip-path: polygon(
    50% 3.6989433%,
    100% 37.426214%,
    81.286893% 100%,
    37.426214% 100%,
    18.713107% 100%,
    0% 37.426214%
  );
}
.polygons-container span.hexagon::before {
  clip-path: polygon(
    25% 13%,
    75% 13%,
    100% 56.5%,
    75% 100%,
    25% 100%,
    0% 56.5%
  );
}
.polygons-container span.rhombus::before {
  clip-path: polygon(50% 0%, 5% 50%, 50% 100%, 95% 50%);
}
