.numpad_wrp {
  all: unset;
  width: 20vw;
  height: 10vw;
  background-color: #15344a;
  box-shadow: 0px 10px 0.8vw 0px #000000c2;

  position: absolute;
  bottom: 50%;
  left: 80%;
  z-index: 999;

  opacity: 0;
  display: none;
  flex-wrap: wrap;
  padding: 0.3vw;
  gap: 0.2vw;
  transition: 0.3s;

  & > .in-calc {
    all: unset;
    background-color: #fefefe;
    text-align: center;
    font-size: 1.1vw;
    border-radius: 0.1vw;
    cursor: pointer;

    flex: 1 2.2vw;
    &.cal_space,
    &.cal_done {
      flex: 4.7vw;
    }

    &:active {
      transform: scale(0.9);
    }
    &:focus {
      outline: 0.1vw solid red;
    }

    &.color_black {
      background-color: #252525;
    }
    &.color_blue {
      background-color: darkblue;
    }
    &.color_skyblue {
      background-color: #0081f7;
    }
    &.color_darkgreen {
      background-color: darkgreen;
    }
    &.color_red {
      background-color: red;
    }
    &.color_orange {
      background-color: rgb(255, 145, 0);
    }
    &.color_yellow {
      background-color: #eed000;
    }
  }
}
